input{
  background-color: var(--backgroundColor);
  border-color: var(--primaryColor);
  border-style: solid;
  border-radius: .5rem;
}

select{
  background-color: var(--backgroundColor);
  border-color: var(--primaryColor);
  border-style: solid;
  border-radius: .5rem;
  cursor: pointer;
}

tr{
  display: flex;
}

td{
  margin: 0.1rem;
  display: flex;
  text-align: center;
  min-width: 2rem;
  min-height: 1rem;
  padding: .5rem;
  border-radius: 0.5rem;
  background-color: var(--backgroundColor);
  justify-content: center;
}

.width-100perc{
  width: 100%;
}

#Bingo-Gen-Shell{
  background-color: var(--backgroundColor);
  margin: 1rem;
  border-radius: 1rem;
  padding: 1rem;
  font-family: cursive;
  font-size: 1.5rem;
  cursor: context-menu;
  color: var(--textColor);
}

#Bingo-Gen-Input-Shell{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}

#Bingo-Gen-Type-Shell{
  background-color: var(--backgroundColor);
  padding: 1rem;
  border-radius: 1rem;
}

#Bingo-Gen-Type-Header{
  display: flex;
  text-align: center;
}

.Bingo-Gen-Type-Header-Inner{
  flex-grow: 1;
  border-radius: 1rem 1rem 0 0;
  cursor: pointer;
}

.Bingo-Gen-Type-Header-Selected{
  background-color: var(--primaryColor);
}

.Bingo-Gen-Type-Header-Unselected{
  background-color: var(--secondaryColor);
}

.Bingo-Gen-Type-Header-Unselected:hover{
  background-color: var(--hoverColor);
}

#Bingo-Gen-Download-Preview{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

#Bingo-Gen-snackbar {
  visibility: hidden;
  position: fixed;
  z-index: 1;
  bottom: 1rem;
  width: 90%;
  display: flex;
  justify-content: center;
}

#Bingo-Gen-snackbar p{
  min-width: 10vw;
  max-width: 50vw;
  background-color: var(--backgroundColor);
  border-color: var(--primaryColor);
  border-width: 0.5rem;
  border-style: solid;
  text-align: center;
  border-radius: 1rem;
  padding: 1rem;
}

#Bingo-Gen-snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;} 
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;} 
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

