.Bingo-Gen-Card-Shell{
    background-color: var(--primaryColor);
    border-radius: 1rem;
    margin: 1rem;
    padding: 1rem;
    height: fit-content;
}

.Bingo-Gen-Card-Shell p{
    margin: 0;
    text-align: center;
    color: var(--backgroundColor);
}

.Bingo-Gen-Card-Cell{
    padding: 1rem;
    min-height: 2rem;    
    container-type: size;
}

.Bingo-Gen-Free-Cell{
    background-color: var(--primaryColor);
    border-radius: 100%;
    padding: 1rem;
}