td{
    color: grey;
}

#Bingo-Gen-Options{
    display: flex;
    background-color: var(--primaryColor);
    border-radius: 1rem;
    padding: 1rem;
    flex-wrap: wrap;
    color: var(--backgroundColor);
}

#Bingo-Gen-Side-Words{
    flex-grow: 1;
    max-height: 30rem;
    overflow-y: scroll;
    scrollbar-width: none;
}

#Bingo-Gen-Side-Words::-webkit-scrollbar{
    display: none;
}
.Bingo-Gen-Side{
    flex-grow: 1;
    max-height: 30rem;
    overflow-y: scroll;
    scrollbar-width: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.Bingo-Gen-Side::-webkit-scrollbar{
    display: none;
}

.Bingo-Gen-Dropdown{
    display: flex;
    padding: 1rem;
}

.Bingo-Gen-Dropdown p{
    margin: 0;
    flex-grow: 1;
}

#Bingo-Gen-Words-Table{
    display: flex;
    flex-direction: column;
}