#Bingo-Gen-Download{
    background-color: var(--primaryColor);
    border-radius: 1rem;
    padding: 1rem;
    margin: 1rem;
    text-align: center;
    width: fit-content;
}

#Bingo-Gen-Download-Dropdown{
    display: flex;
    justify-content: center;
}

#Bingo-Gen-Download-Dropdown p{
    margin: 0;
}

#Bingo-Gen-Download-Button{
    background-color: var(--backgroundColor);
    padding: 1rem;
    border-radius: 1rem;
    width: fit-content;
    margin: 1rem auto 0;
    cursor: pointer;
}

#Bingo-Gen-Download-Button:hover{
    background-color: var(--hoverColor);
}

#Bingo-Gen-Download-Options{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

#Bingo-Gen-Download-Options div{
    margin: 1rem;
}

.Bingo-Gen-Checkbox-shell{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.Bingo-Gen-Checkbox-unchecked{
    width: 1rem;
    height: 1rem;
    border-radius: 0.5rem;
    border-color: var(--backgroundColor);
    border-style: solid;
    background-color: var(--primaryColor);
}

.Bingo-Gen-Checkbox-checked{
    width: 1rem;
    height: 1rem;
    border-radius: 0.5rem;
    border-color: var(--backgroundColor);
    border-style: solid;
    background-color: var(--backgroundColor);
}